@import url(https://fonts.googleapis.com/css2?family=Alef&display=swap);
* {
  font-family: 'Alef', sans-serif !important;
}
body {
  font-family: 'Alef', sans-serif !important;
}

.topbarContainer {
  height: 60px;
  width: 100%;
  background-color: #fff;
  display: flex;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.topbarLeft {
  flex: 3 1;
}
.ResultBoxMain {
  width: 120%;
  border-radius: 8px;
  background: aquamarine;
  position: absolute;
}
.ResultBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: aquamarine;
  padding: 7px;
  cursor: pointer;
}

.logo {
  height: 40px;
  font-size: 24px;
  margin-left: 20px;
  margin-right: 10px;
  font-weight: bold;
  color: #abc9db;
  cursor: pointer;
}

.topbarCenter {
  display: flex;
  justify-content: flex-end;
  flex: 5 1;
}

.searchbar {
  width: 30%;
  height: 30px;
  border: 1px solid #abc9db;
  border-radius: 7px;
  display: flex;
  align-items: center;
}
.logged-nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  min-width: 200px;
  font-size: 18px;
}
.searchIcon {
  font-size: 20px !important;
  margin-left: 10px;
  display: none;
  color: #6097bf;
  direction: ltr;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.top-search {
  width: 100% !important;
  height: 30px !important;
  border: 1px solid #abc9db !important;
  border-radius: 7px !important;
  display: flex !important;
  align-items: center !important;
  padding-right: 35px !important;
}

.SearchInput {
  border: none;
  width: 90%;
  color: #6097bf;
  /* direction: rtl; */
}

.SearchInput:focus {
  outline: none;
}

.topbarRight {
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: white;
  margin-left: 3rem;
  margin-right: 3rem;
}

.topbarLink {
  margin-right: 10px;
  font-size: 18px;
  cursor: pointer;
}

.topbarIcons {
  display: flex;
}

.topbarIconItem {
  margin-right: 15px;
  cursor: pointer;
  position: relative;
}

.tobparIconBadge {
  width: 15px;
  height: 15px;
  background-color: red;
  border-radius: 50%;
  color: white;
  position: absolute;
  top: -5px;
  right: -5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}

.topbarImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  /* .topbarLinks {
    display: none;
  } */
  .searchbar {
    display: none;
  }
  .topbarContainer {
    width: 420px;
  }
  .topbarCenter {
    display: none;
  }
}

.sidebar {
    flex: 2.5 1;
    height: calc(100vh - 50px);
    overflow-y: scroll;
    position: -webkit-sticky;
    position: sticky;
    top: 50px;
  }
  
  ::-webkit-scrollbar {
    width: 5px;
  }
  ::-webkit-scrollbar-track {
    background-color: #f1f1f1;
  }
  ::-webkit-scrollbar-thumb {
    background-color: rgb(179, 179, 179);
  }
  
  .sidebarWrapper {
    padding: 20px;
  }
  
  .sidebarList {
    padding: 0;
    margin: 0;
    list-style: none;
  }
  
  .sidebarListItem {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .sidebarIcon {
    margin-right: 15px;
  }
  
  .sidebarButton {
    width: 150px;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-weight: 500;
  }
  
  .sidebarHr {
    margin: 20px 0;
  }
  
  .sidebarFriendList {
    padding: 5px;
    margin: 0;
    list-style: none;
  }
  .search-results {
    margin: auto;
    height: 100px;
    overflow: auto;
    width: 25%;
    border: solid 1px #ccc;
    border-radius: 15px;
  }

  .none {
    display: none;
  }
.sidebarFriend {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.sidebarFriendImg {
  width: 32px;
  height: 32px;
