.modal {
  font-size: 12px;
  background-color: #fff;
  border: 1px solid #d7d7d7;
  color: #6097bf;
  width: 50vw;
}
.modal > .header {
  width: 98.5%;
  border-bottom: 1px solid gray;
  font-size: 18px;
  text-align: center;
  padding: 5px;
}
.modal > .content {
  width: 100%;
  padding: 10px 5px;
  font-size: 15px;
}
.modal > .actions {
  width: 100%;
  padding: 10px 5px;
  margin: auto;
  text-align: center;
}
.modal > .actions > .button {
  font-size: 12px;
  border-radius: 15px;
  background-color: #fff;
  border: 1px solid #d7d7d7;
  color: #6097bf;
}
.modal > .close {
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -10px;
  top: -10px;
  font-size: 24px;
  background: #ffffff;
  border-radius: 18px;
  border: 1px solid #cfcece;
}
