@import '../../../node_modules/video-react/dist/video-react.css';
.homeContainer {
  display: flex;
}

@media only screen and (max-width: 600px) {
  .homeContainer {
    display: grid;
    max-width: 400px;
  }

  .feed {
    max-width: 405px;
    margin-top: 10px;
  }
  .sidebar {
    height: auto;
  }
  .sidebarFriendList {
    display: none;
  }
  .sidebarWrapper {
    display: none;
  }
  .topbarCenter {
    flex: 12;
  }
  .topbarLeft {
    flex: 6;
  }
  .shareWrapper {
    padding: 2px;
  }
}
.main-image {
  width: 100%;
}
.searchbar-container {
  background-color: #dcecf4;
  display: flex;
  justify-content: center;
  height: 7vh;
  align-items: center;
}
.search-container {
  display: flex;
  flex-direction: column;
}
.searchbar-2 {
  background: #fff;
  width: 25%;
  display: flex;
}
.searchIcon {
}
.img-350 {
  height: 350px;
  width: 350px;
}
.text-container-home {
  width: 35%;
  font-size: 35px;
  font-weight: 400;
  color: #6097bf;
}
.top-image {
  padding-right: 6%;
}
.top-image-container {
  color: #6097bf;
  display: flex;
  justify-content: space-around;
  margin-top: 5rem;
  padding-bottom: 2rem;
  direction: rtl;
  flex-direction: column;
}
.bottom-image-container {
  padding-right: 6%;
  height: 200px;
  background-position: center;
  direction: rtl;
  padding-bottom: 2rem;
  background-image: url('../../assets/leftImageBG.png');
}
.left-img {
  margin: 1rem;
}
.popups-container {
  display: flex;
  justify-content: center;
}
.img-300 {
  width: 300px;
  height: 300px;
}
.imgs-container {
  display: flex;
  justify-content: space-evenly;
  color: #6097bf;
  margin-top: 2rem;
  margin-bottom: 2rem;
  text-align: center;
}

.modal {
  font-size: 12px;
  background-color: #fff;
  border: 1px solid #d7d7d7;
  color: #6097bf;
  width: 50vw;
}
.modal > .header {
  width: 98.5%;
  border-bottom: 1px solid gray;
  font-size: 18px;
  text-align: center;
  padding: 5px;
}
.modal > .content {
  width: 100%;
  padding: 10px 5px;
  font-size: 15px;
}
.modal > .actions {
  width: 100%;
  padding: 10px 5px;
  margin: auto;
  text-align: center;
}
.modal > .actions > .button {
  font-size: 12px;
  border-radius: 15px;
  background-color: #fff;
  border: 1px solid #d7d7d7;
  color: #6097bf;
}
.modal > .close {
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -10px;
  top: -10px;
  font-size: 24px;
  background: #ffffff;
  border-radius: 18px;
  border: 1px solid #cfcece;
}
.video-react-button {
  display: none !important;
}
.player-wrapper {
  position: relative;
}
.vid-text-container {
}
.vid-text-title {
  text-align: center;
  color: #6097bf;
  font-size: 25px;
}
.react-player {
  border-radius: 15px;
  margin-right: 45%;
  padding-top: 22.5% !important;
  width: 40% !important;
  max-width: 40% !important;
  margin-top: -350px;
}
.video-react-poster {
  background-size: cover !important;
}
.slick-slider {
  width: 75%;
}
.slick-next {
  right: 0;
}
.slick-next::before {
  font-size: 20px;
  line-height: 1;
  color: #dcecf4;
}
.slick-prev {
  left: 0;
}
.slick-prev::before {
  font-size: 20px;
  line-height: 1;
  color: #dcecf4;
}
.testimonials {
  text-align: center;
  color: #6097bf;
  display: flex;
  text-align: center;
  color: #6097bf;
  height: 300px;
  justify-content: center;
  width: 99vw;
  align-items: center;
  font-size: 21px;
}
.example-profile {
  display: flex;
  background: #dcecf4;
  padding-top: 3.5%;
  padding-bottom: 3.5%;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
}
.slick-list {
  border-radius: 15px !important;
}
.example-profile-image {
  border-radius: 15px;
  width: 100%;
  height: 75vh;
  overflow: hidden;
  background-position: right center;
  background-repeat: no-repeat;
}
.profile-div {
  background: #abc9db;
  height: 10vh;
  display: flex;
  color: #fff;
  align-items: center;
  justify-content: center;
}
.creation-btn {
  text-decoration: none;
  font-size: 25px;
}
.plans-container {
  display: flex;
  justify-content: center;
}
.plan-container {
  width: 376px;
  height: 500px;
  border: 2px solid #dcecf4;
  box-sizing: border-box;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  margin: 1rem;
  text-align: center;
  padding-top: 3rem;
}
.plans-title {
  color: #6097bf;
  text-align: center;
  margin-top: 4rem;
  margin-bottom: 4rem;
}
.plan-title {
  margin-top: 3rem;
  color: #6097bf;
  text-align: center;
}
.plan-description {
  margin-top: 30%;
  font-size: 20px;
  color: #6097bf;
}
.link-plans {
  margin-top: 3rem !important;
  margin-bottom: 5rem !important;
  display: flex;
  width: 774px;
  height: 60px;
  background: #6097bf;
  border-radius: 30px;
  font-size: 20px;
  align-items: center;
  justify-content: center;
  margin: auto;
  cursor: pointer;
  color: #fff;
}
.plans-section a {
  cursor: pointer;
}
